.container {
  position: relative;
  height: 100%;
  width: 100%;

  display: flex;

  justify-content: center;

  .content {
    position: absolute;
    width: 82.8125%;
    height: 64.6268656717%;
    top: 28.6567164179%;
    display: grid;
    grid-template-columns: 36.0377358491% 63.9622641509%;
    grid-template-rows: 100%;

    &__description {
      display: flex;
      flex-direction: column;

      &__info {
        font-family: "Gilroy-SemiBold";
        font-weight: 600;
        font-size: clamp(1.875rem, calc(-1.9862rem + 4.5226vw), 5.25rem);

        line-height: clamp(2.25rem, calc(-2.3262rem + 5.3601vw), 6.25rem);

        margin-bottom: clamp(1.5rem, calc(0.642rem + 1.005vw), 2.25rem);

        color: #323232;
      }

      &__mail,
      &__phone {
        display: flex;
        flex-direction: column;

        span {
          color: #6b6b6b;
        }

        a {
          text-decoration: none;
          color: #2d5dd9;
        }
      }

      &__phone {
        margin-top: clamp(0.75rem, calc(0.5294rem + 0.2941vw), 1rem);
      }

      &__works {
        margin-top:  clamp(2.75rem, calc(-0.2279rem + 3.9706vw), 6.125rem);

        span {
          color: #2c2f36;
        }

        a {
          text-decoration: underline;
          text-underline-position: under;
          color: #2d5dd9;
        }
      }
    }

    &__imgContainer {
      display: flex;
      height: 100%;

      img {
        max-width: 100%;
        max-height: 100%;
        margin-bottom: auto;
      }
    }
  }
}

@media only screen and (max-width: 1200px) {
  .container {
    flex-direction: column;
    align-items: center;

    &__topSpaser {
      flex: 2;
    }

    &__bottomSpaser {
      flex: 3;
    }

    .content {
      position: initial;
      max-width: 288px;
      height: fit-content;
      grid-template-columns: auto;
      grid-template-rows: 228px 1fr;

      &__description {
        width: 100%;
        grid-area: 2;

        &__info {
          font-size: clamp(1.375rem, calc(-0.3271rem + 8.5106vw), 1.875rem);
          line-height: clamp(1.625rem, calc(-0.5027rem + 10.6383vw), 2.25rem);
        }
      }

      &__imgContainer {
        justify-content: center;
        margin-bottom: 44px;
      }
    }
  }

  .descriptionFont {
    font-size: clamp(0.875rem, calc(0.4495rem + 2.1277vw), 1rem);

    line-height: clamp(1rem, calc(0.3617rem + 3.1915vw), 1.1875rem);
  }
}

.descriptionFont {
  font-family: "Gilroy-Regular";
  font-size: clamp(1rem, calc(-1.0021rem + 2.3451vw), 2.75rem);
  line-height: clamp(1.1875rem, calc(-1.1006rem + 2.6801vw), 3.1875rem);
}
